import React, { cloneElement, useCallback, useRef } from 'react';
import { throttle } from 'lodash';
import { IBaseIconMenuProps } from './interface';
import { Popover } from '../../../../display/popover';
import { IconArrowDown } from '../../../../basic/icon';
import { ToolbarHoverTip } from '../../toolbarHoverTip';
import { useDefaultProp, mergeRef, mergeClassName } from '../../../../common';
import { Wrapper } from './styled';
import { BadgeDot } from '../../../../basic/badge';

const placementOffset = [4, 0] as [number, number];

export const BaseIconMenu = React.memo(React.forwardRef<HTMLDivElement, IBaseIconMenuProps>(
  (props, ref) => {
    const {
      icon,
      title,
      active,
      tooltip,
      disabled,
      size,
      overlay,
      autoClose,
      overlayVisible,
      onVisibleChange,
      overlayStyle,
      badgeDot,
      testid,
      role,
      getContainer,
      placement = 'bottomLeft',
      ...restProps
    } = props;

    const [menuVisible, setMenuVisible] = useDefaultProp(false, overlayVisible, onVisibleChange);

    const cRef = useRef(menuVisible);
    cRef.current = menuVisible;

    const wrapRef = useRef<HTMLDivElement>(null);


    const onClick = useCallback(throttle(() => {
      setMenuVisible(!cRef.current);
    }, 300, { trailing: false }), []);

    const showTitle = size === 'large';

    return (
      <Wrapper
        ref={mergeRef(ref, wrapRef)}
        className={mergeClassName('wdn-toolbar-item', size,  (menuVisible || active) && 'active', disabled && 'disabled')}
        data-testid={testid}
        data-role={role}
        {...restProps}
      >
        <Popover
          content={overlay}
          onVisibleChange={setMenuVisible}
          visible={menuVisible}
          getContainer={getContainer || (() => wrapRef.current || document.body)}
          placementOffset={placementOffset}
          placement={placement}
          animation
          data-testid={`overlay-${testid}`}
        >
          <div style={{ lineHeight: 0 }} onClick={disabled ? undefined : onClick}>
            <ToolbarHoverTip overlayVisible={menuVisible} disabled={disabled} tooltip={tooltip || (!showTitle && !disabled && title)}>
              <div className="wdn-toolbar-icon">
                {cloneElement(icon, { size: size === 'large' ? 'large' : 'normal' })}
                <IconArrowDown className={`wdn-tb-menu-array ${menuVisible ? 'rotate' : ''}`} style={{ width: 10, height: 10 }} />
                {
                  badgeDot && (
                    typeof badgeDot === 'boolean'
                      ? <BadgeDot style={{ position: 'absolute', top: 0, right: 10 }} />
                      : <BadgeDot style={{ position: 'absolute', top: badgeDot.top, right: badgeDot.right + 10 }} />
                  )
                }
              </div>
            </ToolbarHoverTip>
          </div>
        </Popover>
        {showTitle && <div className="wdn-toolbar-title" onClick={disabled ? undefined : onClick}><div className='wdn-tb-title-content'>{title}</div></div>}
      </Wrapper>
    );
  },
));
